.container-jaws {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  margin-bottom: 50px;
  margin-top: 50px;
}

.post {
  margin-bottom: 40px;
}

.post h2 {
  font-size: 2em;
  color: #333;
  margin-bottom: 10px;
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;

}

.post-meta {
  font-size: 0.9em;
  color: #999;
  margin-bottom: 20px;
}

.post p {
  color: #666;
  line-height: 1.6;
  margin-bottom: 20px;
}

.post-image {
  width: 100%;
  max-width: 300px;
  height: auto;
  display: block;
  margin: 0 auto 20px auto;
  border-radius: 8px;
}

.critiques {
  border-top: 2px solid #ddd;
  padding-top: 20px;
}

.critiques h3 {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.critique {
  border-bottom: 1px solid #ddd;
  padding-bottom: 15px;
  margin-bottom: 15px;
}

.critique:last-child {
  border-bottom: none;
}

.critiquer-info {
  font-weight: bold;
  color: #333;
}

.critique p {
  color: #666;
  margin: 5px 0 0;
}

.zoom-meeting {
  border-top: 2px solid #ddd;
  padding-top: 20px;
  margin-top: 20px;
  text-align: center;
}

.zoom-meeting h3 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.zoom-meeting p {
  color: #666;
  margin-bottom: 20px;
}

.btn-zoom {
  background-color: #000;
  color: #000;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn-zoom:hover {
  background-color: #4b4949;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  padding-top: 60px;
}

.modal-content-jaws {
  background-color: #fefefe;
  margin: 5% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 500px;
  border-radius: 8px;
  text-align: left;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.modal-content-jaws label {
  display: block;
  margin-top: 10px;
  font-weight: bold;
}

.modal-content-jaws input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.modal-content-jaws button {
  width: 100%;
  padding: 10px;
  background-color: #000;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
}

.modal-content-jaws button:hover {
  background-color: #4b4949;
}

.modal.show {
  display: block;
}

.small-info-bubble {
  font-size: 0.75em; 
}

.custom-h2 {
  margin: 0;
  padding: 0;
  text-align: left; 
  margin-bottom: 10px;
}